import React from "react";
import style from "./index.scss";
// import Rx from "rxjs";
import { Observable } from "rxjs/Observable";
import { Scheduler } from "rxjs/Scheduler";

import { of, from, interval, combineLatest } from 'rxjs'
import { tap, map } from 'rxjs/operators';

import {
  moveTo,
  line,
  dist,
  angle,
  getVector,
  metaball,
  metaballToPath
} from "./utils";

class Metaballs extends React.Component {
  componentDidMount() {
    const circle1 = document.querySelector("#js-circle1");
    const circle2 = document.querySelector("#js-circle2");
    const connector = document.querySelector("#js-connector");
    const VIEWBOX_SIZE = { W: 1200, H: 400 };
    const SIZES = {
      CIRCLE1: 96,
      CIRCLE2: 64
    };

    // of Observable
    // do Observable  Observable   Observable 
    const circle1$ = of([600, 120]).pipe(
      tap(loc => {
        moveTo(loc, circle1);
      })
    )

    // interval Observable  Observable  IScheduler 
    // map value
    // Scheduler.animationFrame : 
    const circle2$ = interval(0, Scheduler.animationFrame).pipe(
      map(frame => 200 * Math.sin(frame / 500)), //x
      map(x => [600 + x, 120]),
      tap(loc => {
        moveTo(loc, circle2);
      })
    )


    // combineLatest Observables  Observable  Observable  Observable 
    combineLatest(circle1$, circle2$, (circle1Loc, circle2Loc) =>
      metaball(SIZES.CIRCLE1, SIZES.CIRCLE2, circle1Loc, circle2Loc)
    ).subscribe(path => {
      connector.setAttribute("d", path);
    });
  }
  render() {
    return (
      <main className={style.container}>
        <svg
          className={style.svgContainer}
          viewBox="0 0 1200 240"
          preserveAspectRatio="xMidYMid slice"
        >
          <g strokeWidth="6" fill="#fff" stroke="#333">
            <circle id="js-circle1" cx="400" cy="100" r="96" />
            <circle id="js-circle2" cx="400" cy="100" r="64" stroke="none" />
            <path id="js-connector" d="" fill="#333" />
          </g>
        </svg>
      </main>
    );
  }
}

export default Metaballs;



